

import { Direction, Directionality } from '@angular/cdk/bidi';
import { BooleanInput, NumberInput } from '@angular/cdk/coercion';
import {
  ChangeDetectionStrategy,
  ChangeDetectorRef,
  Component,
  ElementRef,
  EventEmitter,
  Input,
  OnChanges,
  OnDestroy,
  OnInit,
  Optional,
  Output,
  SimpleChanges,
  TemplateRef,
  ViewEncapsulation
} from '@angular/core';
import { ReplaySubject, Subject } from 'rxjs';
import { takeUntil } from 'rxjs/operators';
import { ButtonSize, TDSConfigKey, TDSConfigService, WithConfig } from "tds-ui/core/config";
import { gridResponsiveMap, TDSBreakpointEnum, TDSBreakpointService } from 'tds-ui/core/services';
import { TDSI18nService, TDSPaginationI18nInterface } from 'tds-ui/i18n';
import { InputBoolean, InputNumber } from 'tds-ui/shared/utility';


import { PaginationItemRenderContext } from './pagination.types';

const TDS_CONFIG_MODULE_NAME: TDSConfigKey = 'pagination';

@Component({
  selector: 'tds-pagination',
  exportAs: 'tdsPagination',
  preserveWhitespaces: false,
  encapsulation: ViewEncapsulation.None,
  changeDetection: ChangeDetectionStrategy.OnPush,
  template: `
    <ng-container *ngIf="showPagination">
      <ng-container *ngIf="simple; else defaultPagination.template">
        <ng-template [ngTemplateOutlet]="simplePagination.template"></ng-template>
      </ng-container>
    </ng-container>
    <tds-pagination-simple
      #simplePagination
      [disabled]="disabled"
      [itemRender]="itemRender"
      [locale]="locale"
      [pageSize]="pageSize"
      [total]="total"
      [pageIndex]="pageIndex"
      [size]="_size"
      (pageIndexChange)="onPageIndexChange($event)"
    ></tds-pagination-simple>
    <tds-pagination-default
      #defaultPagination
      [size]="_size"
      [itemRender]="itemRender"
      [showTotal]="showTotal"
      [showRefresh]="showRefresh"
      [disabled]="disabled"
      [locale]="locale"
      [showSizeChanger]="showSizeChanger"
      [showQuickJumper]="showQuickJumper"
      [total]="total"
      [pageIndex]="pageIndex"
      [pageSize]="pageSize"
      [pageSizeOptions]="pageSizeOptions"
      [bordered]="bordered"
      (pageIndexChange)="onPageIndexChange($event)"
      (pageSizeChange)="onPageSizeChange($event)"
      (clickRefresh)="onClickRefresh($event)"
    ></tds-pagination-default>
  `,
  host: {
    '[class.tds-pagination-simple]': 'simple',
    '[class.tds-pagination-disabled]': 'disabled',
    '[class.mini]': `!simple && _size === 'small'`,
    '[class.tds-pagination-rtl]': `dir === 'rtl'`
  }
})
export class TDSPaginationComponent implements OnInit, OnDestroy, OnChanges {
  readonly _tdsModuleName: TDSConfigKey = TDS_CONFIG_MODULE_NAME;

  static ngAcceptInputType_disabled: BooleanInput;
  static ngAcceptInputType_showSizeChanger: BooleanInput;
  static ngAcceptInputType_hideOnSinglePage: BooleanInput;
  static ngAcceptInputType_showQuickJumper: BooleanInput;
  static ngAcceptInputType_simple: BooleanInput;
  static ngAcceptInputType_responsive: BooleanInput;
  static ngAcceptInputType_total: NumberInput;
  static ngAcceptInputType_pageIndex: NumberInput;
  static ngAcceptInputType_pageSize: NumberInput;

  @Output() readonly pageSizeChange: EventEmitter<number> = new EventEmitter();
  @Output() readonly pageIndexChange: EventEmitter<number> = new EventEmitter();
  @Input() showTotal: TemplateRef<{ $implicit: number; range: [number, number] }> | null = null;
  @Input() itemRender: TemplateRef<PaginationItemRenderContext> | null = null;
  @Input() @WithConfig() size: ButtonSize = 'sm';
  @Input() @WithConfig() pageSizeOptions: number[] = [10, 20, 30, 40];
  @Input() @WithConfig() @InputBoolean() showSizeChanger = false;
  @Input() @WithConfig() @InputBoolean() showQuickJumper = false;
  @Input() @WithConfig() @InputBoolean() simple = false;
  @Input() @InputBoolean() disabled = false;
  @Input() @InputBoolean() responsive = false;
  @Input() @InputBoolean() hideOnSinglePage = false;
  @Input() @InputNumber() total = 0;
  @Input() @InputNumber() pageIndex = 1;
  @Input() @InputNumber() pageSize = 10;
  @Input() showRefresh :boolean =false;
  @Input() bordered :boolean =true;
  @Output() readonly clickRefresh: EventEmitter<MouseEvent> = new EventEmitter();
  showPagination = true;
  locale!: TDSPaginationI18nInterface;
  _size: ButtonSize = "sm";
  dir: Direction = 'ltr';

  private destroy$ = new Subject<void>();
  private total$ = new ReplaySubject<number>(1);

  validatePageIndex(value: number, lastIndex: number): number {
    if (value > lastIndex) {
      return lastIndex;
    } else if (value < 1) {
      return 1;
    } else {
      return value;
    }
  }

  onPageIndexChange(index: number): void {
    const lastIndex = this.getLastIndex(this.total, this.pageSize);
    const validIndex = this.validatePageIndex(index, lastIndex);
    if (validIndex !== this.pageIndex && !this.disabled) {
      this.pageIndex = validIndex;
      this.pageIndexChange.emit(this.pageIndex);
    }
  }

  onPageSizeChange(size: number): void {
    this.pageSize = size;
    this.pageSizeChange.emit(size);
    const lastIndex = this.getLastIndex(this.total, this.pageSize);
    if (this.pageIndex > lastIndex) {
      this.onPageIndexChange(lastIndex);
    }
  }

  onTotalChange(total: number): void {
    const lastIndex = this.getLastIndex(total, this.pageSize);
    if (this.pageIndex > lastIndex) {
      Promise.resolve().then(() => {
        this.onPageIndexChange(lastIndex);
        this.cdr.markForCheck();
      });
    }
  }

  getLastIndex(total: number, pageSize: number): number {
    return Math.ceil(total / pageSize);
  }

  constructor(
    private i18n: TDSI18nService,
    private cdr: ChangeDetectorRef,
    private breakpointService: TDSBreakpointService,
    protected tdsConfigService: TDSConfigService,
    @Optional() private directionality: Directionality,
    private elementRef: ElementRef
  ) {
    // TODO: move to host after View Engine deprecation
    this.elementRef.nativeElement.classList.add('tds-pagination');
  }

  ngOnInit(): void {
    this.i18n.localeChange.pipe(takeUntil(this.destroy$)).subscribe(() => {
      this.locale = this.i18n.getLocaleData('Pagination');
      this.cdr.markForCheck();
    });

    this.total$.pipe(takeUntil(this.destroy$)).subscribe(total => {
      this.onTotalChange(total);
    });

    this.breakpointService
      .subscribe(gridResponsiveMap)
      .pipe(takeUntil(this.destroy$))
      .subscribe(bp => {
        if (this.responsive) {
          this._size = bp === TDSBreakpointEnum.xs ? 'sm' : 'md';
          this.cdr.markForCheck();
        }
      });

    this.directionality.change?.pipe(takeUntil(this.destroy$)).subscribe((direction: Direction) => {
      this.dir = direction;
      this.cdr.detectChanges();
    });

    this.dir = this.directionality.value;
  }

  ngOnDestroy(): void {
    this.destroy$.next();
    this.destroy$.complete();
  }

  ngOnChanges(changes: SimpleChanges): void {
    const { hideOnSinglePage, total, pageSize, size } = changes;
    if (total) {
      this.total$.next(this.total);
    }
    if (hideOnSinglePage || total || pageSize) {
      this.showPagination =
        (this.hideOnSinglePage && this.total > this.pageSize) || (this.total > 0 && !this.hideOnSinglePage);
    }

    if (size) {
      this._size = size.currentValue;
    }
  }
  onClickRefresh(e:MouseEvent)
  {
    this.clickRefresh.emit(e);
  }
}
