

import {
  ChangeDetectionStrategy,
  Component,
  ElementRef,
  EventEmitter,
  Input,
  OnChanges,
  Output,
  SimpleChanges,
  ViewEncapsulation
} from '@angular/core';
import { ButtonSize } from "tds-ui/core/config";
import { TDSPaginationI18nInterface } from 'tds-ui/i18n';
import { toNumber } from 'tds-ui/shared/utility';


@Component({
  selector: 'div[tds-pagination-options]',
  preserveWhitespaces: false,
  encapsulation: ViewEncapsulation.None,
  changeDetection: ChangeDetectionStrategy.OnPush,
  template: `
    <div class='inline-flex items-center' *ngIf="showSizeChanger">
    <tds-select
    class="tds-pagination-options-size-changer"
    
    [disabled]="disabled"
    [size]="size"
    [ngModel]="pageSize"
    (ngModelChange)="onPageSizeChange($event)"
    [data]="listOfPageSizeOption"
    textField="label"
    valueField="value"
  >     
  </tds-select>
      <div class='ml-2 min-w-max'>
        {{locale.items_per_page}}
      </div>
    </div>
   
    <div class="tds-pagination-options-quick-jumper flex items-center " *ngIf="showQuickJumper">
      {{ locale.jump_to }}
      <tds-form-field class="w-20 mx-3">     
        <input autocomplete="off" tdsInput autocomplete="off" 
         [disabled]="disabled" [size]="size" (keydown.enter)="jumpToPageViaInput($event)" />    
      </tds-form-field>
      {{ locale.page }}
    </div>
  `
})
export class TDSPaginationOptionsComponent implements OnChanges {
  @Input() size: ButtonSize = 'md';
  @Input() disabled:boolean = false;
  @Input() showSizeChanger = false;
  @Input() showQuickJumper = false;
  @Input() locale!: TDSPaginationI18nInterface;
  @Input() total = 0;
  @Input() pageIndex = 1;
  @Input() pageSize = 10;
  @Input() pageSizeOptions: number[] = [];
  @Output() readonly pageIndexChange = new EventEmitter<number>();
  @Output() readonly pageSizeChange = new EventEmitter<number>();
  listOfPageSizeOption: Array<{ value: number; label: string }> = [];

  constructor(private elementRef: ElementRef) {
    // TODO: move to host after View Engine deprecation
    this.elementRef.nativeElement.classList.add('tds-pagination-options');
  }

  onPageSizeChange(size: number): void {
    if (this.pageSize !== size) {
      this.pageSizeChange.next(size);
    }
  }

  jumpToPageViaInput($event: Event): void {
    const target = $event.target as HTMLInputElement;
    const index = Math.floor(toNumber(target.value, this.pageIndex));
    this.pageIndexChange.next(index);
    target.value = '';
  }

  trackByOption(_: number, option: { value: number; label: string }): number {
    return option.value;
  }

  ngOnChanges(changes: SimpleChanges): void {
    const { pageSize, pageSizeOptions, locale } = changes;
    if (pageSize || pageSizeOptions || locale) {
      this.listOfPageSizeOption = [...new Set([...this.pageSizeOptions, this.pageSize])].map(item => ({
        value: item,
        label: `${item}`
      }));
    }
  }
}
